import { Link } from "gatsby"
import React from "react"
import Tag from "react-bulma-components/esm/components/tag"

const NewsletterTitle = (node) => {
  return (
    <div className={"newsletter-title"}>
      <Link to={`/${node.node.slug}`}>
        <small className={"date"}>
          {new Intl.DateTimeFormat("de-DE", {
            year: "numeric",
            month: "2-digit",
            day: "2-digit",
          }).format(new Date(node.node.published))}
        </small>
        <h3 className={"h3"}>{node.node.title}</h3>{" "}
      </Link>
      <div className={"parts"}>
        {node.node.newsletter.parts.map((part, index) => (
          <span key={index}>
            {part.emoji.icon} {part.title}&nbsp;
          </span>
        ))}
      </div>
      <Tag.Group>
        {node.node.tags.map((tag, index) => (
          <Tag key={tag}>#{tag}</Tag>
        ))}
      </Tag.Group>
    </div>
  )
}

export default NewsletterTitle
